import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import Alert from "react-bootstrap/Alert";
import axios from "axios";
import { ThemeConsumer } from "react-bootstrap/esm/ThemeProvider";
import Button from "react-bootstrap/Button";
import posed from "react-pose";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAt } from "@fortawesome/free-solid-svg-icons";
import {
  faBitcoin,
  faFacebook,
  faInstagram,
  faLinkedin,
} from "@fortawesome/free-brands-svg-icons";

const FadeDiv = posed.div({
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
});
const emailRe = /[^@]+@[^@]+\.[^@]+/;
const styles = {
  center: { display: "flex", justifyContent: "center", alignItems: "center" },
  socialButton: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    padding: 3,
    width: 30,
    height: 30,
    marginLeft: 10,
    marginRight: 10,
    borderRadius: 100,
  },
};
const INIT_STATE = {
  name: "",
  email: "",
  message: "",
  error: null,
  submitted: false,
  sending: false,
  sent: false,
};
class SocialLinks extends Component {
  constructor(props) {
    super(props);
    this.state = { ...INIT_STATE };
  }
  onSubmit = (event) => {
    this.setState({ submitted: true });
    const isInvalid =
      !this.state.name ||
      !emailRe.test(this.state.email) ||
      !this.state.message;
    if (!isInvalid) {
      const dest = "https://formspree.io/f/xnqonepp";
      const data = this.state;
      this.setState({ sending: true });
      axios
        .post(dest, { data })
        .then((res) => {
          this.setState({ ...INIT_STATE, sent: !this.state.sent });
          setTimeout(() => {
            this.setState({ sent: !this.state.sent });
          }, 3200);
        })
        .catch((error) => {
          this.setState({ error, sending: false });
        });
    }
  };
  onChange = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };
  render() {
    return (
      <div style={styles.center}>
        <Button
          variant="primary"
          style={styles.socialButton}
          target="_blank"
          href="https://www.linkedin.com/in/winton-nathan-roberts/"
        >
          <FontAwesomeIcon
            color="white"
            icon={faLinkedin}
            size="lg"
            style={{ margin: 0, padding: 0 }}
          />
        </Button>
        <Button
          variant="primary"
          style={styles.socialButton}
          target="_blank"
          href="https://www.facebook.com/crypticdeluge/"
        >
          <FontAwesomeIcon
            color="white"
            icon={faFacebook}
            size="lg"
            style={{ margin: 0, padding: 0 }}
          />
        </Button>
        <Button
          variant="primary"
          style={styles.socialButton}
          target="_blank"
          href="https://www.instagram.com/sydneyhandsanitiser/"
        >
          <FontAwesomeIcon
            color="white"
            icon={faInstagram}
            size="lg"
            style={{ margin: 0, padding: 0 }}
          />
        </Button>
      </div>
    );
  }
}

export default SocialLinks;
